}
.stat-value.status-rejected {
  background-color: #f8d7da;
  color: #721c24;
  border: none;
}
.stat-value.status-withdrawn {
  background-color: #e2e3e5;
  color: #495057;
  border: none;
}

.document-counter {
  width: 100%;
  margin-bottom: 20px;
}

.document-stats {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  gap: 18px;
  background: #f8f9fa;
  border-radius: 8px;
