}

.payment-form {
  padding: 20px;
  max-width: 500px;
  margin: 0 auto;
}

.payment-form .form-group {
  margin-bottom: 20px;
}

.payment-form .error-message {
  color: #fa755a;
  margin-top: 8px;
  font-size: 14px;
}

.payment-form .btn-submit {
  width: 100%;
  padding: 12px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.payment-form .btn-submit:hover {
  background-color: #45a049;
}

.payment-form .btn-submit:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.payment-info {
  margin-top: 15px;
  padding: 12px;
  background-color: #f8f9fa;
  border-left: 4px solid #007bff;
  border-radius: 4px;
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.payment-info i {
  color: #007bff;
  font-size: 1.2rem;
  margin-top: 2px;
}

.payment-info p {
  margin: 0;
  color: #495057;
  font-size: 0.95rem;
  line-height: 1.4;
}

.payment-status {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: var(--dl-font-size-small);
  font-weight: 500;
}

.payment-status.paid {
  background-color: #d4edda;
  color: #155724;
}

.payment-status.unpaid {
  background-color: #f8d7da;
  color: #721c24;
}
